@import 'theme_variables';

// product desc
// primary_block
.primary_block {
  margin-bottom: 40px;
}

.top-hr {
  background: $top-line-color;
  height: 5px;
  margin: 2px 0 31px;
}

.product-heading-h5 {
  color: $gray-light;
  font-weight: 200;
  margin: 3px 0 8px;
  font-size: 19px;
  font-family: $font-custom;
}

.box-security .product-heading-h5 {
  margin: 3px 0 -8px;
  @media  (max-width: $screen-md-max) { //max 1199px
    margin-bottom: 8px;
  }
}

.primary_block .pb-left-column img {
  max-width: 100%;
  height: auto;
}

a.fancybox-nav {
  outline: 0;
}

// .pb-left-column
.pb-left-column {

  #image-block {
    position: relative;
    display: block;
    cursor: pointer;
    padding: 5px;
    border: 1px solid $product-image-border;
    background: $light-background;
    @media (max-width: $screen-xs-max) { //max 767px
      width: 280px;
      margin: 0 auto;
    }

    img {
      background: $base-box-bg;
      width: 100%;
    }

    #view_full_size {

      .span_link {
        position: absolute;
        bottom: 20px;
        left: 50%;
        margin-left: -68px;
        display: block;
        padding: 10px 0;
        text-align: center;
        line-height: 22px;
        color: $boulder;
        width: 136px;
        font-weight: 700;
        background: lighten($white, 50%);

        &::after {
          font-family: $font-icon;
          color: $silver;
          font-size: 20px;
          line-height: 22px;
          content: '\f00e';
          font-weight: normal;
          margin: 0 0 0 4px;
        }

        &:hover::after {
          color: $base-text-color;
        }
      }
    }

    a.jqzoom {

      div div {

        img {
          width: auto;
          max-width: 10000px;
        }

        .zoomWrapper {
          border: 0;
        }
      }
    }

    .zoomPad {
      z-index: auto !important;
    }

    .new-box,
    .sale-box {
      z-index: 5002;
    }
  }
}


// miniatures
.pb-left-column #views_block {
  margin-top: 10px;
  @media (max-width: $screen-xs-max) { //max 767px
    width: 280px;
    margin: 10px auto 0;
  }
}

#thumbs_list {
  overflow: hidden;
  float: left;
  width: 392px;
  @media (min-width: $screen-md) and (max-width: $screen-md-max) { //min 992px max 1199px
    width: 294px;
  }
  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { // min 768px max 991px
    width: 164px;
  }
  @media (max-width: $screen-xs-max) { //max 767px
    width: 196px;
  }

  ul#thumbs_list_frame {
    list-style-type: none;
    padding-left: 0;
    overflow: hidden;
    height: 90px;
  }

  li {
    float: left;
    height: 90px;
    width: 90px;
    cursor: pointer;
    border: 1px solid $product-image-border;
    margin-right: 8px;
    line-height: 0;
    @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { //min 768px max 991px
      width: 76px;
      height: 76px;
      margin-right: 6px;
    }

    a {
      display: block;
      border: 3px solid $light-border-color;
      background: $base-box-bg;

      &:hover,
      &.shown {
        border-color: $product-image-border;
      }
    }

    img {
      border: 1px solid $light-border-color;
      width: 100%;
      height: 100%;
      @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { //min 768px max 991px
        width: 68px;
        height: 68px;
      }
    }
  }
}

.show-right-column.hide-left-column,
.hide-right-column.show-left-column {

  #thumbs_list {
    width: 294px;
    @media (min-width: $screen-md) and (max-width: $screen-md-max) { //min 992px max 1199px
      width: 194px;
    }
    @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { // min 768px max 991px
      width: 82px;
    }
    @media (max-width: $screen-xs-max) { //max 767px
      width: 196px;
    }
  }

  span.view_scroll_spacer {
    width: 27px;
    padding-left: 2px;
    @media (min-width: $screen-md) and (max-width: $screen-md-max) { //min 992px max 1199px
      width: 44px;
      padding-left: 16px;
    }
    @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { // min 768px max 991px
      width: 41px;
      padding-left: 13px;
    }
    @media (max-width: $screen-xs-max) { //max 767px
      width: 45px;
      padding-left: 14px;
    }
  }
}

span.view_scroll_spacer {
  float: left;
  width: 39px;
  padding-left: 12px;
  @media (min-width: $screen-md) and (max-width: $screen-md-max) { //min 992px max 1199px
    width: 45px;
    padding-left: 18px;
  }
  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { // min 768px max 991px
    width: 30px;
    padding-left: 3px;
  }
  @media (max-width: $screen-xs-max) { //max 767px
    width: 45px;
    padding-left: 18px;
  }
}

#view_scroll_left,
#view_scroll_right {
  margin-top: 38px;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { //min 768px max 991px
    margin-top: 28px;
  }

  &::before {
    padding-left: 2px;
    color: $scroll-button-color;
    font-family: $font-icon;
    font-size: 20px;
    line-height: 22px;
  }

  &:hover::before {
    color: $scroll-button-hover-color;
  }
}

#view_scroll_right {
  float: left;

  &::before {
    content: '\f138';
  }
}

#view_scroll_left {
  margin-top: 0;

  &::before {
    content: '\f137';
    padding-right: 2px;
  }
}

.resetimg {
  padding: 10px 0 0;

  #resetImages i {
    margin-right: 6px;
  }
}

.view_scroll_spacer {
  margin-top: 38px;
  @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { //min 768px max 991px
    margin-top: 28px;
  }
}

// link list
#usefull_link_block {
  list-style-type: none;

  li {
    margin: 0 0 8px;
    @media (min-width: $screen-sm) and (max-width: $screen-sm-max) {   //min 768px max 991px
      float: none !important;
    }

    &:first-child {
      margin: 0 0 8px;
      border: 0;
      padding: 0;
    }

    a {
      color: $boulder;
      font-weight: 700;
      position: relative;
      padding-left: 30px;
      line-height: 22px;
      display: inline-block;

      &::before {
        font-family: $font-icon;
        color: $base-text-color;
        font-size: 19px;
        line-height: 24px;
        position: absolute;
        top: -2px;
        left: 0;
        font-weight: normal;
      }

      &:hover {
        color: $black;
      }
    }

    &.print {
      @media (min-width: $screen-sm) and (max-width: $screen-sm-max) { //min 768px max 991px
        margin: 8px 0 0;
        padding: 0;
        border: 0;
      }

      a::before {
        content: '\f02f';
      }
    }

    &.sendtofriend {

      a::before {
        content: '\f003';
      }
    }

    &#left_share_fb {
      clear: both;
      float: none;

      a {
        padding-left: 18px;
      }

      a::before {
        content: '\f09a';
      }
    }

    &#favoriteproducts_block_extra_remove,
    &#favoriteproducts_block_extra_add,
    &#favoriteproducts_block_extra_removed,
    &#favoriteproducts_block_extra_added {
      cursor: pointer;
      font-weight: 700;
      clear: both;
      float: none;
      margin: 0 0 8px;
      border: 0;
      padding: 0;
      position: relative;

      &::before {
        font-family: $font-icon;
        color: $base-text-color;
        font-size: 19px;
        line-height: 24px;
        position: absolute;
        top: -2px;
        left: 0;
        font-weight: normal;
        content: '\f08a';
      }

      &:hover {
        color: $black;
      }
    }

    &#favoriteproducts_block_extra_removed::before {
      content: '\f004';
    }
  }
}

// pb-left-column
.pb-center-column {
  margin-bottom: 30px;

  h1 {
    padding-bottom: 16px;
    font-size: 20px;
    color: $product-name-color;
  }

  p {
    margin-bottom: 10px;
  }

  #product_reference {
    margin-bottom: 6px;
  }

  #short_description_block {
    color: $description-color;

    .buttons_bottom_block {
      display: none;
    }

    #short_description_content {
      padding: 0 0 15px;
      word-wrap: break-word;

      p {
        line-height: 18px;
      }
    }
  }

  // form
  #buy_block {
    margin-top: 10px;

    label {
      display: inline-block;
      width: 125px;
      font-weight: 700;
      font-size: 12px;
      text-align: right;
    }

    p {

      &.price {
        padding: 0;
      }
    }
  }

  #color_picker p {
    padding-bottom: 5px;
    font-weight: 700;
  }
}

#product p#loyalty {
  padding: 17px 0 15px 35px;
  border-top: $main_border;
  border-bottom: $main_border;
  margin: 10px 0 -5px;
  position: relative;

  b {
    color: $base-text-color;
  }

  &::before {
    content: '\f024';
    font-family: $font-icon;
    color: $silver;
    font-size: 26px;
    line-height: 28px;
    position: absolute;
    top: 16px;
    left: 0;
  }
}

#page .rte {
  padding: 0 20px 20px;
  word-wrap: break-word;

  p {
    margin-bottom: 1em;
    min-height: 1px;
  }
}

#attributes {

  fieldset {
    padding-bottom: 3px;

    label {
      vertical-align: top;
      display: block;
    }
  }

  .attribute_list {
    width: 100%;

    .form-control {
      max-width: 230px;

      option {
        background: $light-background;
        color: $description-color;
      }
    }

    ul {
      clear: both;

      li {
        float: left;
        margin-right: 10px;
      }
    }

    input.attribute_radio {
      float: left;
      margin-left: 3px;
    }

    div.radio,
    div.radio span,
    div.radio input {
      float: none;
      display: inline-block;
    }

    #color_to_pick_list {
      list-style-type: none;

      li {
        float: left;
        margin: 0 3px 3px 0;
        padding: 1px;
        border: 1px solid $base-border-color;
        clear: none;
        width: 26px;
        height: 26px;

        a.color_pick {
          display: block;
          height: 22px;
          width: 22px;
          cursor: pointer;

          img {
            display: block;
            height: 22px;
            width: 22px;
          }
        }

        &.selected {
          border: 1px solid $tundora;
        }
      }
    }
  }
}

.product_attributes,
.box-cart-bottom {
  padding: 19px 19px 17px;
}

.box-cart-bottom {
  padding: 0;
}

#short_description_content,
.page-product-box .rte {

  a {
    color: $mine-shaft;
    text-decoration: underline;
  }

  a.btn {
    text-decoration: none;
  }
}

.box-info-product {
  background: $list-info-bg;
  border: 1px solid $silver;
  border-top: 1px solid $nobel;
  border-bottom: 1px solid $nobel;

  p {
    margin-bottom: 7px;
  }

  .exclusive {
    padding: 0;
    border-top: 1px solid $allports;
    border-right: 1px solid $allports;
    border-left: 1px solid $allports;
    border-bottom: 1px solid $daintree;
    position: relative;
    display: block;

    &::before {
      font-family: $font-icon;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      color: $light-text-color;
      font-size: 25px;
      line-height: 47px;
      text-shadow: 0 1px $orient;
      content: '\f07a';
      width: 51px;
      text-align: center;
      border: 1px solid $cerulean;
      @media (max-width: $screen-sm-max) { //max 991px
        display: none;
      }
    }

    &.added {

      &::before {
        content: '\f00c';
      }
    }

    &.added.disabled,
    &.disabled {

      &::before {
        height: 51px;
        content: '\f110';
        animation: 2s linear 0s normal none infinite spin;
        border: 0;
      }
    }

    &::after {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 51px;
      content: '';
      width: 1px;
      @media (max-width: $screen-sm-max) { // max 991px
        display: none;
      }
    }

    span {
      font-weight: 700;
      font-size: 20px;
      line-height: 22px;
      border: 1px solid $cerulean;
      text-shadow: 0 1px $orient;
      padding: 12px 36px 14px 60px;
      color: $light-text-color;
      display: block !important;
      @media  (max-width: $screen-md-max) { //max 1199px
        padding: 12px 22px 14px 55px;
        font-size: 14px;
      }
      @media (max-width: $screen-sm-max) {  //max 991px
        padding: 8px 12px 10px;
        text-align: left;
      }
    }
  }
}

#center_column.col-sm-9 {

  .col-md-3 {

    .box-info-product {
      background: $list-info-bg;
      border: 1px solid $silver;
      border-top: 1px solid $nobel;
      border-bottom: 1px solid $nobel;

      .exclusive {
        padding: 0;
        border-top: 1px solid $allports;
        border-right: 1px solid $allports;
        border-left: 1px solid $allports;
        border-bottom: 1px solid $daintree;
        position: relative;
        display: block;

        &::before {
          font-family: $font-icon;
          position: relative;
          top: 0;
          left: 0;
          bottom: 0;
          color: $light-text-color;
          font-size: 25px;
          line-height: 47px;
          text-shadow: 0 1px $orient;
          content: '\f07a';
          z-index: 2;
          width: 51px;
          text-align: center;
          border: 0;
          @media (max-width: $screen-sm-max) { //max 991px
            display: none;
          }
        }

        &.added {

          &::before {
            content: '\f00c';
          }
        }

        &.added.disabled,
        &.disabled {

          &::before {
            content: '\f110';
            animation: 2s linear 0s normal none infinite spin;
            border: 0;
          }
        }

        &::after {
          background: none;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 51px;
          content: '';
          width: 1px;
          z-index: 2;
          @media (max-width: $screen-sm-max) { // max 991px
            display: none;
          }
        }

        span {
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          border: 0;
          border-top: 1px solid $allports;
          text-shadow: 0 1px $orient;
          padding: 12px 16px 14px;
          color: $light-text-color;
          display: block !important;
          @media  (max-width: $screen-md-max) { //max 1199px
            font-size: 16px;
          }
          @media (max-width: $screen-sm-max) {  //max 991px
            padding: 8px 12px 10px;
            text-align: center;
          }
        }
      }
    }
  }
}

// table_data_sheet
.table-data-sheet {
  border-bottom: $main_border;
  width: 100%;
  margin: 0 0 20px;
  background: $light-background;

  tr {
    border-top: $main_border;

    &.even {
      background: $tr-even-bg;
    }

    td {
      padding: 10px 20px 11px;

      &:first-child {
        border-right: $main_border;
        width: 30%;
        font-weight: 700;
        color: $base-text-color;
      }
    }
  }
}

// table product-discounts
table.table-product-discounts {
  border: 0;
  border-bottom: $main_border;
  border-right: $main_border;
  width: 50%;
  margin: 0 0 20px;
  background: $light-background;
  @media (max-width: $screen-xs-max) { //max 767px
    width: 100%;
  }

  tr th {
    background: $table-background;
    color: $base-text-color;
    font-weight: 700;
    border: 0;
  }

  tr td,
  tr th {
    border-left: $main_border;
    width: 33%;
    padding: 10px 20px 11px;
    border-top: $main_border;
    border-right: 0;
    text-shadow: 0;
    text-transform: none;
    font-weight: 13px;
    line-height: 18px;
    border-bottom: 0;
  }
}

// accessories
.accessories-block {

  .block_description {
    display: none;
  }

  .exclusive span {
    font-size: 13px;
    line-height: 18px;
    padding: 2px 8px 3px;
    text-decoration: none;

    &:focus,
    &:active {
      text-decoration: none;
    }
  }

  .product-name {
    margin-bottom: 5px;

    a {
      font-size: 15px;
      line-height: 18px;
      color: $product-name-color;

      &:hover {
        color: $product-name-hover-color;
      }
    }
  }

  div ul li {

    a {
      display: inline-block;
    }

    &.s_title_block {

      a,
      span {
        font-weight: 700;
      }
    }
  }
}

// quantity_wanted 
#quantity_wanted_p {

  input {
    width: 78px;
    height: 27px;
    padding: 0 6px;
    float: left;
    border: $main_border;
    line-height: 27px;
  }

  .btn {
    float: left;
    margin-left: 3px;
  }

  label {
    display: block;
    margin-bottom: 7px;
  }
}

// availability_statut
#availability_statut span#availability_label,
#availability_date_label {
  display: inline-block;
  width: 125px;
  font-weight: 700;
  font-size: 12px;
  text-align: right;
}

#quantityAvailable {
  color: $base-text-color;
  font-size: 13px;
  line-height: 18px;
  font-weight: 700;
}

#availability_value,
#last_quantities {
  color: $light-text-color;
  font-weight: 700;
  line-height: 18px;
  display: inline-block;
  padding: 3px 8px 4px;
  margin-left: 5px;
}

#last_quantities {
  background: $sunshade;
  border: 1px solid $zest;
  padding: 4px 8px 3px;
  position: relative;
  top: 1px;
  margin-left: 0;
}

#availability_statut,
#pQuantityAvailable {
  display: inline-block;
}

#pQuantityAvailable span {
  font-weight: 700;
  color: $base-text-color;
}

// box-security
.box-security {
  border-top: 1px solid $silver;
  padding: 19px 19px 6px;
}

// prices
.content_prices {
  padding: 13px 19px;
}

.discount {
  display: none;
}

.our_price_display {
  font-weight: 600;
  font-size: 29px;
  line-height: 32px;
  font-family: $font-custom;
  color: $product-price-color;
  @media (max-width: $screen-sm-max) { //max 991px
    font-size: 26px;
    line-height: 28px;
  }

  .price {
    font-weight: 600;
    font-size: 29px;
    line-height: 32px;
    color: $product-price-color;
    @media (max-width: $screen-sm-max) { //max 991px
      font-size: 26px;
      line-height: 28px;
    }
  }
}

#old_price {
  padding-bottom: 15px;
  font-size: 13px;
  text-decoration: line-through;
  display: inline-block;
  font-family: $font-custom;
  line-height: 23px;
}

#reduction_percent,
#reduction_amount {
  margin-right: 10px;
  padding: 1px 2px;
  font-weight: 600;
  font-family: $font-custom;
  font-size: 21px;
  line-height: 23px;
  color: $light-text-color;
  background: $pomegranate;
  border: 1px solid $persian-red;

  span {
    display: block;
  }

  .hide-left-column.hide-right-column & {
    display: inline-block;
  }
}

// online_only
.pb-center-column p.online_only {
  font-weight: 700;
  font-size: 13px;
  color: $pomegranate;
  text-align: left;
}

.buttons_bottom_block {
  clear: both;
  padding: 13px 19px 0;

  #wishlist_button {
    font-weight: 700;
  }
}

// SEND TO FRIEND (pop-in)
#send_friend_form {
  width: 555px;
  overflow: hidden;
  @media (max-width: $screen-xs-max) { // max 767px
    width: 280px;
  }

  .product {

    img {
      margin: 0 0 10px;
      border: 1px solid $base-border-color;
    }

    p {
      padding: 0 ;
      color: $base-text-color;

      .product_name {
        font-size: 12px;
        color: $black;
      }
    }
  }

  .form_container {
    padding: 10px 15px;
    border: 1px solid $base-border-color;
    background: $base-box-bg;

    p {

      &.intro_form {
        padding-bottom: 6px;
        font-weight: 700;
        font-size: 13px;
        color: $base-text-color;
      }

      &.txt_required {
        padding-bottom: 0;
        color: $dove-gray;
      }
    }

    .text {
      padding-bottom: 10px;

      label {
        display: block;
        padding: 0;
        width: 100%;
        font-size: 12px;
      }

      input {
        padding: 0 5px;
        height: 22px;
        width: 100%;
        border: 1px solid $base-border-color;
        font-size: 12px;
        color: $dove-gray;
      }
    }
  }

  .submit {
    margin-top: 20px;
    padding-bottom: 0;
    text-align: right;
  }
}

// PERSONNALISATION

p.infoCustomizable {
  margin-bottom: 30px;
}

ul#uploadable_files,
ul#text_fields {
  list-style-type: none;
}

.customizableProductsFile,
.customizableProductsText {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid $base-border-color;
}

.customizationUploadLine {
  padding-bottom: 10px;

  label {
    display: block;
    vertical-align: top;
  }

  textarea {
    width: 50%;
    border: $main_border;
    color: $text-color;
  }
}

.customizationUploadBrowse img + a {
  position: relative;
  top: -51px;
}

#customizedDatas {
  float: right;
}

// Quick View Styles
#product.content_only {

  div {

    &.primary_block {
      margin-bottom: 0;
      @media (max-width: $screen-sm-max) { //max 991px
        margin-right: 0;
      }

      div {

        &.pb-left-column {
          padding: 20px 20px 5px 35px;
          width: 47%;
          @media (max-width: $screen-xs - 1)  {  // max 479px
            width: 100%;
            float: none;
          }

          span.view_scroll_spacer {
            width: 38px;
            padding-left: 10px;
          }

          #thumbs_list {
            width: 392px;
          }
        }

        &.pb-center-column {
          padding: 20px 20px 60px 0;
          width: 27.5%;
          margin-bottom: 0;
          min-height: 623px;
          height: 100%;
          @media (max-width: $screen-sm-max) {  //max 991px
            min-height: 440px;
          }
          @media (max-width: $screen-xs - 1)  { // max 479px
            width: 100%;
            float: none;
          }

          .product_comments_block_extra {
            padding-right: 0;
          }

          @media (max-width: $screen-xs - 1)  {   // max 479px
            .comments_advices li {
              display: block;
            }

            .comments_advices a.reviews {
              margin: 0;
              padding: 0;
              border: 0;
            }
          }

          #ShareDiv {
            position: absolute;
            bottom: 8px;
          }
        }

        &.pb-right-column {
          width: 25.5%;
          padding-right: 15px;
          padding-left: 0;

          .box-info-product {
            border-top-width: 0;
            border-right: 0;
            border-bottom: 0;
            min-height: 623px;
            height: 100%;
            position: relative;
            padding-bottom: 125px;
            @media (max-width: $screen-sm-max) {  //max 991px
              border: $main_border;
            }

            #add_to_cart .exclusive {
              display: block;
              width: 100%;

              &::before,
              &::after {
                display: block;
              }

              span {
                font-size: 20px;
                text-align: center;
                @media (max-width: $screen-md-max) { //max 1199px
                  padding: 12px 22px 14px 70px;
                  font-size: 20px;
                }
              }
            }

            .box-cart-bottom {
              right: 0;
              left: 0;
            }
          }

          #attributes .attribute_list {

            .form-control {
              width: 230px;
            }
          }

          .product_attributes {
            padding-top: 0;
          }
        }
      }
    }
  }
}

.addsharethisinner .stButton .stArrow {
  display: none !important;
}

@media (max-width: 1050px) {
  #product.content_only {

    div,
    section {

      &.primary_block {

        div {

          &.pb-left-column {
            width: 100%;
            padding-right: 15px;

            #image-block,
            #views_block {
              max-width: 470px;
              width: 100%;
              margin-left: auto;
              margin-right: auto;
            }

            #thumbs_list {

              li {
                width: 90px;
                height: 90px;

                img {
                  width: 82px;
                  height: 82px;
                }
              }
            }
          }

          &.pb-center-column {
            width: 50%;
            float: left;
            padding-left: 35px;
          }

          &.pb-right-column {
            width: 50%;
            float: left;

            .box-info-product {
              border-top-width: 1px;
              min-height: 1px;
            }
          }
        }
      }
    }
  }
}

// IE code

.ie8 {

  .pb-left-column {

    #image-block {

      #view_full_size {

        .span_link {
          background: $white;
          border: 1px solid $base-border-color;
        }
      }
    }
  }

  .box-security {
    filter: none;
  }
}
